'use strict';

angular.module('videoHub')
  .directive('detailsNavBar', function() {
    return {
        'restrict': 'E',
        'templateUrl': 'shared/detailsnavbar/detailsnavbar.html',
        'transclude': true,
        'controller': function($scope, $rootScope, logoUrl, $location, CurrentUser) {
            $scope.logoUrl = logoUrl;
            CurrentUser.$simplified().then(function(data){
              $scope.currentUserName = data.displayName;
            });
        }
    };
  });
