'use strict';
angular.module('videoHub')
  //TODO: These constants are redundant between the detail pages. Should be merged together into one mega-constant.
  .constant('VideoDetailControllerConstants', {
    SAVED_ALERT_MESSAGE: 'Saved',
    SOMETHING_WENT_WRONG_ALERT_MESSAGE: 'Something went wrong with saving the Video. Please call your friendly sysadmin.',
    PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE: 'Please fill in all required fields.',
    DELETE_SUCCESSFUL: 'Delete successful'
  })
  .controller('VideoDetailController', function($scope, $routeParams, VideoService, $location, AlertService, VideoDetailControllerConstants, DropdownFilterService, Restangular) {
    $scope.video = {};

    $scope.getVideo = function() {
      if (_.has($routeParams, 'videoId')) {
        return VideoService.one($routeParams.videoId).get().then(
          $scope.setVideo,
          function(data) {
            console.log(data);
          }
        );
      } else {
        $scope.video = {};
        Restangular.restangularizeElement(null, $scope.video, 'videos');
        $scope.setVideo($scope.video);
      }
    };

    $scope.setVideo = function(data) {
      if (_.isEmpty(data.input)) {
        data.input = '';
      }
      $scope.video = data;
      if (_.isUndefined($scope.video.tags)) {
        $scope.video.tags = [];
      }

      $scope.autocompleteFields.tagObjects = $scope.video.tags;
      $scope.dropdownFields = {};
      $scope.video.tags = _.pluck($scope.video.tags, 'id');

      if (!_.isUndefined(data.channel)) {
        DropdownFilterService.setChannel(data.channel);
      }
      if (!_.isUndefined(data.series)) {
        DropdownFilterService.setSeries(data.series);
      }
      if (!_.isUndefined(data.sponsor)) {
        $scope.dropdownFields.sponsor = data.sponsor;
      }
    };

    $scope.save = function() {
      if ($scope.videoDetailForm.$valid) {
        if (_.has(DropdownFilterService.data.channel, 'id')) {
          $scope.video.channel = DropdownFilterService.data.channel.id;
        } else {
          $scope.video.channel = null;
        }

        if (_.has(DropdownFilterService.data.series, 'id')) {
          $scope.video.series = DropdownFilterService.data.series.id;
        } else {
          $scope.video.series = null;
        }

        if (_.has(DropdownFilterService.data.season, 'id')) {
          $scope.video.season = DropdownFilterService.data.season.id;
        } else {
          $scope.video.season = null;
        }

        if (_.has($scope.dropdownFields.sponsor, 'id')) {
          $scope.video.sponsor = $scope.dropdownFields.sponsor.id;
        }
        $scope.video.tags = _.pluck($scope.autocompleteFields.tagObjects, 'id');
        $scope.video.save().then($scope.successCallback, displaySomethingWentWrongAlert);
      } else {
        AlertService.error(VideoDetailControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    $scope.successCallback = function(video) {
      displaySaveSuccessfulAlert().then(function() {
        if (!_.has($routeParams, 'videoId')) {
          $location.path('cms/video/' + video.id + '/');
        }
      });
    };

    function displaySaveSuccessfulAlert() {
      return AlertService.success(VideoDetailControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(VideoDetailControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }

    $scope.delete = function() {
      $scope.video.remove().then(function() {
        AlertService.success(VideoDetailControllerConstants.DELETE_SUCCESSFUL, true).then(function() {
          $location.path('cms/');
        });
      });
    };

    $scope.autocompleteFields = {};
    $scope.getVideo();
  });
