'use strict';

angular.module('videoHub')
  .constant('SeriesDetailControllerConstants', {
    SAVED_ALERT_MESSAGE: 'Saved',
    SOMETHING_WENT_WRONG_ALERT_MESSAGE: 'Something went wrong with saving the Series. Please call your friendly sysadmin.',
    PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE: 'Please fill in all required fields.',
    DELETE_SUCCESSFUL: 'Delete successful'
  })
  .controller('SeriesDetailController', function($scope, $routeParams, SeriesService, $location, AlertService, SeriesDetailControllerConstants, Restangular) {
    $scope.series = {};

    $scope.getSeries = function() {
      if (_.has($routeParams, 'seriesId')) {
        return SeriesService.one($routeParams.seriesId).get().then(
          function(data) {
            $scope.series = data;
            $scope.dropdownFields.channel = data.channel;
            $scope.dropdownFields.sponsor = data.sponsor;
          },
          function(data) {
            console.log(data);
          }
        );
      } else {
        $scope.series = {};
        Restangular.restangularizeElement(null, $scope.series, 'series');
        $scope.dropdownFields.channel = {};
        $scope.dropdownFields.sponsor = {};
      }
    };

    $scope.save = function() {
      if ($scope.seriesDetailForm.$valid) {
        if (_.has($scope.dropdownFields.channel, 'id')) {
          $scope.series.channel = $scope.dropdownFields.channel.id;
        } else {
          $scope.series.channel = null;
        }

        if (_.has($scope.dropdownFields.sponsor, 'id')) {
          $scope.series.sponsor = $scope.dropdownFields.sponsor.id;
        } else {
          $scope.series.sponsor = null;
        }
        $scope.series.save().then($scope.successCallback, displaySomethingWentWrongAlert);
      } else {
        AlertService.error(SeriesDetailControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    $scope.successCallback = function(series) {
      displaySaveSuccessfulAlert().then(function() {
        if (!_.has($routeParams, 'seriesId')) {
          $location.path('cms/series/' + series.id);
        }
      });
    };

    function displaySaveSuccessfulAlert() {
      return AlertService.success(SeriesDetailControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(SeriesDetailControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }

    $scope.delete = function() {
      AlertService.info('If you would like to delete a series, please contact your friendly sysadmin.', true);
    };

    $scope.dropdownFields = {};
    $scope.getSeries();
  });
