'use strict';
angular.module('videoHub')
  .constant('ChannelDetailControllerConstants', {
    SAVED_ALERT_MESSAGE: 'Saved',
    SOMETHING_WENT_WRONG_ALERT_MESSAGE: 'Something went wrong with saving the Channel. Please call your friendly sysadmin.',
    PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE: 'Please fill in all required fields.',
    DELETE_SUCCESSFUL: 'Delete successful'
  })
  .controller('ChannelDetailController', function($scope, $routeParams, ChannelService, $location, AlertService, ChannelDetailControllerConstants, Restangular) {
    $scope.channel = {};

    $scope.getChannel = function() {
      if (_.has($routeParams, 'channelId')) {

        return ChannelService.one($routeParams.channelId).get().then(
          function(data) {
            $scope.channel = data;
            $scope.dropdownFields.partner = $scope.channel.partner;
          },
          function(data) {
            console.log(data);
          }
        );
      } else {
        $scope.channel = {};
        Restangular.restangularizeElement(null, $scope.channel, 'channels');
      }
    };

    $scope.save = function() {
      if ($scope.channelDetailForm.$valid) {
        if (_.has($scope.dropdownFields.partner, 'id')) {
          $scope.channel.partner = $scope.dropdownFields.partner.id;
        } else {
          $scope.channel.partner = null;
        }
        $scope.channel.save().then($scope.successCallback, displaySomethingWentWrongAlert);
      } else {
        AlertService.error(ChannelDetailControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    $scope.successCallback = function(channel) {
      displaySaveSuccessfulAlert().then(function() {
        if (!_.has($routeParams, 'channelId')) {
          $location.path('cms/channel/' + channel.id + '/');
        }
      });
    };

    function displaySaveSuccessfulAlert() {
      return AlertService.success(ChannelDetailControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(ChannelDetailControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }

    $scope.delete = function() {
      AlertService.info('If you would like to delete a channel, please contact your friendly sysadmin.', true);
    };

    $scope.dropdownFields = {};
    $scope.getChannel();
  });
